.nav-btn {
  position: relative; }
  .nav-btn:after {
    background: none repeat scroll 0 0 transparent;
    content: "";
    display: block;
    height: 0.1rem;
    background-color: #0f457f;
    transition: width 0.4s ease 0s;
    width: 0; }
  .nav-btn:hover {
    text-decoration: none; }
    .nav-btn:hover:after {
      width: 100%; }

.navbar-nav .nav-btn {
  color: #337ab7; }
