@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
.custom-card {
  transition: box-shadow 0.5s cubic-bezier(0.22, 0.61, 0.36, 1); }
  .custom-card:hover {
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.3); }
  .custom-card .wrapper {
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    min-height: 5.7rem;
    position: relative;
    overflow: hidden;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
    transition: all 0.5s ease 0s; }
    @media (min-width: 576px) {
      .custom-card .wrapper {
        min-height: 15rem; } }
    .custom-card .wrapper:hover {
      opacity: 0.7; }
      .custom-card .wrapper:hover .data {
        transform: translateY(-1em);
        transition: transform 0.3s; }
  .custom-card .data {
    transform: translateY(0);
    transition: transform 0.3s; }
    .custom-card .data .content {
      padding: 1em;
      position: relative;
      z-index: 1; }
      .custom-card .data .content .stats {
        display: flex;
        align-items: center;
        flex-wrap: wrap; }
  .custom-card .title {
    margin-top: 20px; }
  .custom-card .date {
    background-color: #77d7b9;
    color: #fff;
    padding: 0.8em; }
    .custom-card .date span {
      display: block;
      text-align: center; }
  .custom-card .content {
    background-color: #fff; }
    .custom-card .content .title {
      color: #337ab7; }
    .custom-card .content p {
      color: #000; }
