.custom-navbar {
  background-color: #f8f8f8;
  box-shadow: 0 0 10px rgba(14, 14, 14, 0.26);
  transition: background 0.5s;
  padding: 0.5rem 1.5rem; }
  .custom-navbar .navbar-toggler {
    border: 0; }
  .custom-navbar .container .navbar-brand {
    color: black;
    background: transparent; }
    .custom-navbar .container .navbar-brand .brand-logo {
      background: #000;
      border-radius: 3px;
      color: #fff;
      padding: 1px 5px 2px; }
    .custom-navbar .container .navbar-brand:hover {
      background-color: #f2f2f2;
      color: #034378;
      cursor: pointer; }
  .custom-navbar.is-fixed {
    background: transparent; }
    .custom-navbar.is-fixed .btn-link {
      color: #fff; }
    @media (max-width: 767.98px) {
      .custom-navbar.is-fixed {
        background: #fff; }
        .custom-navbar.is-fixed .btn-link {
          color: #000; }
        .custom-navbar.is-fixed .navbar-brand {
          color: #000; } }
