.intro-header {
  background: #fff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  height: 100%;
  margin-left: auto; }
  @media (max-width: 992px) {
    .intro-header {
      margin: 15px; } }
  .intro-header .about-me .avatar {
    margin: 1rem;
    border-radius: 50%;
    height: 120px;
    width: 120px;
    transition: transform 0.5s; }
    .intro-header .about-me .avatar:hover {
      transform: scale(1.2); }
  .intro-header .about-me .name {
    color: #034378;
    text-decoration: none; }
    .intro-header .about-me .name :hover {
      color: #337ab7; }
  .intro-header .custom-icon i {
    transition: background 1s cubic-bezier(0.22, 0.61, 0.36, 1);
    background: #034378;
    border-radius: 50%; }
  .intro-header .custom-icon:hover {
    border-radius: 50%; }
    .intro-header .custom-icon:hover i {
      background: #337ab7; }
  .intro-header .subscription {
    margin-top: 0.825rem;
    margin-bottom: 0.825rem; }
